







































































































































































































































































import { Component, Vue } from "vue-property-decorator";
//
import Banner from "../../components/Banner/Banner.vue";
//vuex-class
import { Getter, Action } from "vuex-class";
//
import BaiduMap from "vue-baidu-map";
//
Vue.use(BaiduMap, {
  ak: "20DplgXMKRoNMqil1glwuOQuiFa98uME"
});

@Component({
  components: {
    Banner: Banner //
  }
})
export default class Index extends Vue {
  @Getter news: any;
  @Action getNews: any;

  //
  private created() {
    this.getNews();
  }


  //
  private flag = {
    videoShowFlag: -1, //
    scenicShowFlag: -1, //
    mapClickFlag: 0 //
  };
  private center = {lng: 119.050247, lat: 31.667598}; //
  private zoom = 15; //
  private startPoint = { lng: 119.050247, lat: 31.667598 }; //
  private end = {lng: 118.9958, lat: 31.651122}; //

  //
  ///
  private videoShow() {
    this.flag.videoShowFlag = 1;
    const body = document.querySelector("body") as HTMLElement;
    body.style.overflow = "hidden";
  }
  private videoHide() {
    this.flag.videoShowFlag = -1;
    const body = document.querySelector("body") as HTMLElement;
    //HTMLAudioElement
    const video = document.querySelector(
      "#home_video_show"
    ) as HTMLAudioElement;
    body.style.overflow = "";
    video.pause();
  }
  ///
  private scenicShow(num: number) {
    this.flag.scenicShowFlag = num;
  }
  private scenicHide() {
    this.flag.scenicShowFlag = -1;
  }
  //
  private mapClick(num: number) {
    this.flag.mapClickFlag = num;
    if (num == 0) {
      this.startPoint.lng = 119.050247;
      this.startPoint.lat = 31.667598;
    } else if (num == 1) {
      this.startPoint.lng = 119.085505;
      this.startPoint.lat = 31.673605;
    } else if (num == 2) {
      this.startPoint.lng = 119.031409;
      this.startPoint.lat = 31.711224;
    }
  }
}
