
















import { Component, Vue } from "vue-property-decorator";

@Component
export default class Banner extends Vue {
  private num = 0; //key
  private screenWidth = document.body.offsetWidth; //

  //
  private imgSetWidth() {
    const imgs = document.querySelectorAll("#list img") as NodeListOf<
      HTMLElement
    >;
    for (let i = 0; i < imgs.length; i++) {
      imgs[i].style.width = this.screenWidth.toString() + "px";
    }
  }
  //
  private showButton() {
    const buttons = document.querySelectorAll("#buttons span") as NodeListOf<
      HTMLElement
    >;
    //
    for (let i = 0; i < buttons.length; i++) {
      if (buttons[i].className == "on") {
        buttons[i].className = "";
      }
    }
    //
    buttons[this.num].className = "on";
  }
  //vue
  private mounted() {
    const imgsList = document.getElementById("list") as HTMLElement; //
    const listWidth = 3 * this.screenWidth; //
    imgsList.style.width = listWidth.toString() + "px"; //
    this.imgSetWidth(); //
    const timer = setInterval(() => {
      this.num++;
      if (this.num == 3) {
        this.num = 0;
      }
      const offSetWidth = -this.num * this.screenWidth; //
      imgsList.style.left = offSetWidth.toString() + "px"; //
      this.showButton(); //
    }, 3000);
    //
    this.$once("hook:beforeDestroy", () => {
      clearInterval(timer);
    });
  }
}
